.container {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 30px;
}

@media (max-width: 1700px) {
  .container {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 500px) {
  .container {
    grid-template-columns: 1fr;
  }
}

.item {
  max-width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  align-self: start;
  justify-self: center;
}

.thumbnail {
  width: 280px;
  height: 240px;
  margin-bottom: 15px;
}

.title {
  font-size: 28px;
  margin-bottom: 2px;
  margin-top: 0px;
  text-align: center;
}

.description {
  margin-top: 5px;
  font-size: 22px;
}
